.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
 /* background-color: rebeccapurple; */
}

.featuredItem {
  flex: 1;
  margin: 10px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
   transition: .8s ease-in-out;
}

.featuredItem:hover{
    background-color: black;
    color: white;
      width: 100%;
      opacity: 0.7;
     }
 

.featuredTitle{
    font-size: 20px;
}
.featuredright {
    float: right;
}
.featuredleft {
    float: left;
}
.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}

.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}

.featuredIcon.negative{
    color: black;
}

.featuredSub{
    font-size: 15px;
    color: gray;
}